import './dev-web.style.scss'
import Footer from '../../../components/footer/footer.component'
import SubNavHeader from '../../../components/shared/sub-nav-header/sub-nav-header.component'
import headerImg from '../../../assets/img/Illustration Dev.svg'
import blueArrow from '../../../assets/img/ArrowBleu.svg'
import blueGrid from '../../../assets/img/BG-PB.svg'

const DevWeb = () => {
    return (
        <div className='dev-web'>
            <SubNavHeader 
            title="Votre <span className='underline'>Site Web</span><br/> Votre
            <span className='blue'> Histoire.</span>"
            citation='Le dveloppement web est la cl pour transformer vos ides <br/> en ralit numrique.'
            subtitle="Sites Web sur Mesure"
            subcontent="Les sites web qui rpondent parfaitement  <strong>vos besoins.</strong> Nos dveloppeurs web expriments conoivent des sites web entirement personnaliss, adapts  <strong>votre image de marque</strong> et  <strong>vos objectifs.</strong> Nous travaillons en troite collaboration avec vous pour donner vie  votre vision en ligne."
            img={headerImg}
            />
            <section className='e-com'>
                <h1 className='ecom-title'>E-Commerce Performant 
                <span><img className='blueArrow' src={blueArrow} alt='blue arrow'/></span>
                </h1>
                <p className='ecom-content'>Si vous tes dans le commerce en ligne, nous pouvons crer <strong>une boutique en ligne puissante et performante</strong>  pour votre entreprise. Nos solutions e-commerce vous permettent de grer facilement vos produits, vos commandes et vos paiements en ligne. Maximisez vos ventes en ligne avec un site e-commerce conu sur mesure.</p>
                <img src={blueGrid} className='blueGrid' alt='blueGrid'/>
            </section>
            <section className="web-content">
                <div className='content-element'>
                    <h1 className='element-title'>
                        UX / UI
                        <span><img className='blueArrow' src={blueArrow} alt='blueArrow'/></span>
                    </h1>
                    <p className='element-content'>Un site web doit offrir une exprience utilisateur exceptionnelle. Nous nous efforons de crer des <strong>designs web modernes et intuitifs</strong> qui garantissent une navigation fluide pour vos visiteurs. Notre objectif est de rendre <strong>votre site web engageant et accessible</strong> sur tous les appareils.</p>
                </div>
                <div className='content-element'>
                    <h1 className='element-title'>
                        Maintenance et Mises  Jour
                        <span><img className='blueArrow' src={blueArrow} alt='blueArrow'/></span>
                    </h1>
                    <p className='element-content'>Nous ne nous contentons pas de crer votre site web, nous nous engageons galement  le <strong>maintenir en parfait tat.</strong> Nous effectuons rgulirement des mises  jour de scurit, des <strong>amliorations de performance</strong> et des ajustements selon vos besoins. Vous pouvez compter sur nous pour que votre site fonctionne de manire optimale en permanence.</p>
                </div>
            </section>
        

            <Footer />
        </div>
    )
}

export default DevWeb