import './veille.style.scss'
import SubNavHeader from '../../../components/shared/sub-nav-header/sub-nav-header.component'
import headerImg from '../../../assets/img/Illustration Veille.svg'
import SubContent from './subcontent/subcontent.component'
import Footer from '../../../components/footer/footer.component'
const Veille = () => {
    return (
        <div>
            <SubNavHeader 
            title="Un service pour <span className='underline'>rester  l'ffut</span>
            <br/> des <span className='blue'>tendances & opportunits.</span>"
            citation="Gardez une longueur d'avance avec notre service de <br/> veille oprationnelle."
            subtitle="Anticipez les tendances "
            subcontent="Notre service de veille vous permet de rester inform des <strong>dernires tendances</strong> et des dveloppements importants dans votre secteur d'activit. Nous surveillons en permanence les actualits pertinentes, et nous vous fournissons des <strong>rapports rguliers</strong> pour vous aider  prendre des dcisions claires."
            img={headerImg}
            />
            <SubContent />
            <Footer/>
    </div>
    )
}

export default Veille