import './ads.style.scss'

import headerImg from '../../../assets/img/Illustration Ads.svg'
import SubNavHeader from '../../../components/shared/sub-nav-header/sub-nav-header.component'
import Footer from '../../../components/footer/footer.component'
import blueArrow from '../../../assets/img/ArrowBleu.svg'
import blueGrid from '../../../assets/img/BG-PB.svg'
import greyGrid from '../../../assets/img/BG-PG.svg'

const Ads = () => {
    return (
        <div className='ads'>
            <SubNavHeader 
                title="Boostez votre <span className='underline'>marque </span> avec
                <br/> des <span className='blue'> campagnes cibles.</span>"
                citation="La publicit en ligne est un moyen puissant d'atteindre votre public cible, <br/>  d'accrotre votre visibilit et d'obtenir des rsultats concrets."
                subtitle="Publicit cibles"
                subcontent="<strong>Atteignez votre public idal</strong> avec des publicits cibles. Nos campagnes publicitaires en ligne sont conues pour mettre en avant <strong>votre entreprise</strong> auprs des personnes les plus susceptibles de s'intresser <strong> vos produits </strong> ou <strong>services.</strong> Nous identifions votre public cible pour vous assurer de toucher les bonnes personnes au bon moment."
                img={headerImg}
            />
            <section className='campagne'>
                    <h1 className='simple-title'>
                        Campagnes Performantes
                        <span><img className='blueArrow' src={blueArrow} alt='blueArrow'/></span>
                    </h1>
                    <p className='simple-content'>Des <strong>campagnes publicitaires performantes</strong> sont essentielles pour obtenir des rsultats tangibles. Nous optimisons constamment vos campagnes pour <strong>maximiser votre retour sur investissement.</strong> Grce  des stratgies adaptes  votre entreprise, nous vous aidons  obtenir <strong> des rsultats concrets,</strong> que ce soit en termes de ventes, de prospects ou de notorit.
                    </p>
                    <img className="blueGrid" src={blueGrid} alt='blueGrid' />
            </section>
            <section className='ads-double'>
            <div className='double-element'>
                <h1 className='double-title'>
                    Diversit des Plateformes Publicitaires
                    <span><img className='blueArrow' src={blueArrow} alt='blueArrow'/></span>
                </h1>
                <p className='double-content'>Nous matrisons la publicit en ligne sur diverses plateformes, de Google Ads  Facebook Ads en passant par TikTok et Instagram. En utilisant les plateformes les plus appropries pour votre entreprise, nous largissons votre porte et maximisons votre visibilit sur le web.
                </p>
            </div>
            <div className='double-element'>
                <h1 className='double-title'>
                    Suivi et Rapports
                    <span><img className='blueArrow' src={blueArrow} alt='blueArrow'/></span>
                </h1>
                <p className='double-content'>La transparence est essentielle. Nous fournissons des rapports dtaills sur les performances de vos annonces pour que vous puissiez suivre les rsultats de prs. Comprenez ce qui fonctionne le mieux et prenez des dcisions claires pour optimiser vos campagnes publicitaires.
                </p>
            </div>
            </section>
            <section className='strategie'>
                <h1 className='simple-title'>
                    Personnalisation de Votre Stratgie
                    <span><img className='blueArrow' src={blueArrow} alt='blueArrow'/></span>
                </h1>
                <p className='simple-content'>Chaque entreprise est unique, c'est pourquoi nous personnalisons votre stratgie publicitaire en fonction de vos besoins spcifiques. Nous comprenons vos objectifs, votre secteur d'activit et votre public cible pour crer des campagnes publicitaires adaptes  votre entreprise.
                </p>
                <img className='greyGrid' src={greyGrid} alt='greyGrid'/>
                <img className='greyGrid' src={greyGrid} alt='greyGrid'/>
            </section>

            <Footer/>
        </div>
    )
}

export default Ads