import { Link } from 'react-router-dom'

import grid from '../../../../assets/img/BG-PB.svg'
import ArrowEffect from '../../../../components/shared/arrowEffect/arrowEffect.component'
import './solution.style.scss'



const SolutionBubble = ({title, link, content, gridVisible}) => {


    return (
        <div className='bubble btn'>
        <Link to={link} className='bubble-link'>
            <h1 className='bubble-title'>{title}</h1>
            <p className='bubble-content'>{content}</p>
            <ArrowEffect />
        </Link>
        <img className={`blue-grid ${gridVisible}`} src={grid} alt='grid of circle'/> 
        </div>
            )
        }
        
        
        const Solution = () => {
            return (
            <div className='solution-container' id='solution'>
                    <div className='top-container'>
                        <h3 className='solution-title'>Nos solutions</h3>
                        <p className='solution-subtitle'>Pour vous</p>
                        <p className='solution-citation'>Nous sommes fiers de cultiver l'image de marque de nos clients et de les aider  atteindre leur potentiel en ligne</p>
                    </div>
                <div className='bubble-container'>
                    <SolutionBubble title="Graphisme" link="graphisme" content="Diffrenciez vous de vos concurrents et renforcez la crdibilit de votre marque." gridVisible="hide"/>

                    <SolutionBubble title="Vido" link="video" content="Crez des contenus vido cratifs et adapts pour renforcer votre prsence en ligne." gridVisible="hide"/>

                    <SolutionBubble title="Social management" link="social" content="Grez efficacement votre prsence en ligne grce  notre service de gestion de rseaux et de serveurs Discord." gridVisible=""/>
                    
                    <SolutionBubble title="Dev Web" link="devWeb" content="Crez des sites web innovants et fonctionnels pour propulser votre prsence en ligne." gridVisible="hide"/>

                    
                    <SolutionBubble title="Veille" link="veille" content="Gagnez du temps et de la prcision dans votre prise de dcision grce  notre service de veille oprationnelle." gridVisible="hide"/>
                    
                    <SolutionBubble title="SEO" link="seo" content="Diffrenciez vous de vos concurrents et renforcez la crdibilit de votre marque." gridVisible="hide"/>
                    <SolutionBubble title="Formations" link="formation" content="Boostez votre carrire avec nos formations." gridVisible="hide"/>
                    <SolutionBubble title="Ads" link="ads" content="Diffrenciez vous de vos concurrents et renforcez la crdibilit de votre marque." gridVisible="hide"/>
                    <SolutionBubble title="eBooks" link="formation" content="Trouvez l'inspiration et les cls de la russite sur la cration d'agence et les stratgies marketing." gridVisible="hide"/>

                
                </div>
            </div>
    )
}

export default Solution